.dot-plot-container {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.dot-plot {
    overflow: auto;
    width: 100%; 
    display: flex;
    flex-direction: column;
    align-items: center;
}

.top-header-plot {
    text-align: center;
    margin-left: 110px;
    margin-bottom: 30px;
}

.index-title-dot-plot-contents{
    display:flex;
}

.index-title {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    text-align: center;
}

.dot-plot-contents {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding-left: 5px;
    row-gap: 30px;
}

.dot-chart-container {
    display: flex;
    flex-direction: column;
    padding-left: 5px;
    list-style-type: none;
    column-gap: 10px;
    row-gap: 10px;
}

.dot-chart {
    display: flex;
    column-gap: 18px;
    box-shadow: 0 2px 8px 0 #63636333;
    display: flex;
    border: solid 1px;
    border-radius: 6px;
    padding: 15px;
}

.the-dots {
    display: flex;
    align-items: center;
    column-gap: 55px;
}

.all-dots-container {
    margin-right: auto;
    display: flex;
    flex-direction: column;
    row-gap: 25px;
}

.all-dots {
    display: flex;
    align-items: center;
    column-gap: 6px;
    /* width: 600px; */
}

.dot-column-level-1 {
    writing-mode: vertical-lr;
    transform: rotate(-180deg);
    width: 10px;
    margin-left: 35px;
}

.dot-column-level-2 {
    width: 80px;
    text-align: center;
}

.column-header {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: solid white 1px;
}

.dot {
    border-radius: 50%;
    width: 18px;
    height: 18px;
    border: solid 1px;
}

.legend-container {
    display: flex;
    flex-direction: column; 
}

.legends-content-container {
    display: flex;
    flex-direction: column;
    row-gap: 6px;
}

.legends-content {
    display: flex;
    align-items: center;
    column-gap: 5px;
}

.legend-symbol {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: solid 1px;
}


@media (max-width: 844px) {
    .column-header {
        width: 10px;
        font-size: 12px;
    }

    .dot-plot{
        display:unset;
    }

    .dot-chart{
        padding:10px;
        align-items: center;
    }

    .dot-column-level-1 {
        font-size: 14px;
    }

    .dot-column-level-2 {
        font-size: 14px;
    }

    .dot {
        width: 10px;
        height: 10px;
    }

    .legend-symbol {
        width: 10px;
        height: 10px;
    }

    .legend-text {
        font-size: 14px;
    }
}




/* width */
::-webkit-scrollbar {
    width: 10px;
    height: 8px;

}


/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 999px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}